import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, DatePicker } from 'antd';
import {
  getReportDisciplineById,
  updateReportDiscipline,
  createReportDiscipline,
} from 'services/reportDiscipline';
import { dateToString } from 'utils/formatUtil';
import moment from 'moment';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    approvalNumber: '',
    approvalDate: '',
    description: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formReportDisciplines' })
class FormReportDisciplinesPage extends Component {
  state = {
    reportDisciplinesId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ reportDisciplinesId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getReportDisciplineById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { reportDisciplinesId } = this.state;
    try {
      if (!reportDisciplinesId) {
        await createReportDiscipline({
          approvalNumber: values.approvalNumber,
          approvalDate: dateToString(values.approvalDate._d, 'YYYY/MM/DD'),
          description: values.description,
        });
      } else {
        await updateReportDiscipline({
          id: reportDisciplinesId,
          approvalNumber: values.approvalNumber,
          approvalDate: dateToString(values.approvalDate._d, 'YYYY/MM/DD'),
          description: values.description,
        });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const dateValueFirstForEdit = dateToString(initialValue.approvalDate, 'DD/MM/YYYY');
    const dateFormat = 'DD/MM/YYYY';

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };

    return (
      <Card>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item hasFeedback label="Số quyết định" {...formItemLayout}>
              {getFieldDecorator('approvalNumber', {
                initialValue: initialValue.approvalNumber,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ngày quyết định" hasFeedback>
              {getFieldDecorator('approvalDate', {
                initialValue: moment(dateValueFirstForEdit, dateFormat),
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<DatePicker format={dateFormat} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Nội dung" hasFeedback>
              {getFieldDecorator('description', {
                initialValue: initialValue.description,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormReportDisciplinesPage {...props} />
  </Page>
));



