import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnCancel extends Component {
  states = '';

  render() {
    return (
      <Button {...this.props} className={`btnAction btnCancel ${this.props.className}`} icon="">
        {this.props.title}
      </Button>
    );
  }
}

BtnCancel.defaultProps = {
  className: '',
  title: 'Hủy',
};



