import { request } from 'utils';

const prefix = '/api/students';

export const getReportDisciplines = ({ page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: '/DisciplineNote',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createReportDiscipline = ({ approvalNumber, approvalDate, description }) =>
  request({
    prefix,
    url: `/DisciplineNote`,
    data: { approvalNumber, approvalDate, description },
    method: 'POST',
  });

export const deleteDiscipline = id =>
  request({
    prefix,
    url: `/DisciplineNote/${id}`,
    method: 'DELETE',
  });

export const getReportDisciplineById = id =>
  request({
    prefix,
    url: `/DisciplineNote/${id}`,
    method: 'GET',
  });

export const updateReportDiscipline = ({ id, approvalNumber, approvalDate, description }) =>
  request({
    prefix,
    url: `/DisciplineNote/${id}`,
    data: { id, approvalNumber, approvalDate, description },
    method: 'PUT',
  });



