import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnSave extends Component {
  states = '';

  render() {
    return (
      <Button {...this.props} className={`btnAction btnSave ${this.props.className}`} icon="save">
        {this.props.title}
      </Button>
    );
  }
}

BtnSave.defaultProps = {
  className: '',
  title: 'Lưu dữ liệu',
};



